import { useEffect, useState } from 'react';
import {
    PanelBody,
    Button,
    Spinner,
    TextControl,
    FormToggle,
    TextareaControl 
} from '@wordpress/components';
import { fetchData, saveData } from './api';

const EmailCustomizerTab = ({ openPopup }) => {
    const [isSaving, setIsSaving] = useState(false);
    const [loading, setLoading] = useState(true);
    const [data, setData] = useState({});
    const [modifiedFields, setModifiedFields] = useState({});

    useEffect(() => {
        fetchData(gmpqcw_wp_ajax.getsettings, gmpqcw_wp_ajax.nonce)
            .then((fetchedData) => {
                setData(fetchedData);
                setLoading(false);
            })
            .catch((error) => {
                setLoading(false);
                console.error('Error fetching settings:', error);
            });
    }, []);

    const updateField = (field, value) => {
        setData((prevData) => ({
            ...prevData,
            [field]: value,
        }));

        setModifiedFields((prevFields) => ({
            ...prevFields,
            [field]: value,
        }));
    };

    const saveSettings = () => {
        if (Object.keys(modifiedFields).length === 0) {
            openPopup('No changes to save.');
            return;
        }

        setIsSaving(true);
        saveData(gmpqcw_wp_ajax.savedata, gmpqcw_wp_ajax.nonce, modifiedFields)
            .then((responseData) => {
                setIsSaving(false);
                if (responseData.success) {
                    openPopup('Settings saved!');
                    setModifiedFields({}); // Clear modified fields after saving
                } else {
                    openPopup('Failed to save settings.');
                }
            })
            .catch((error) => {
                setIsSaving(false);
                console.error('Error saving settings:', error);
                openPopup('Failed to save settings.');
            });
    };

    return (
        <PanelBody>
            {loading ? (
                <Spinner />
            ) : (
                <>
                    <table className="GMadminTable">
                        <tr>
                            <th>
                                <label>Recipient Email</label>
                            </th>
                            <td>
                                <TextControl
                                    onChange={(value) => updateField('gmpqcw_reci_email', value)}
                                    value={data.gmpqcw_reci_email}
                                />
                            </td>
                        </tr>
                        <tr>
                            <th>
                                <label>Email Subject</label>
                            </th>
                            <td>
                                <TextControl
                                    onChange={(value) => updateField('gmpqcw_email_sub', value)}
                                    value={data.gmpqcw_email_sub}
                                />
                            </td>
                        </tr>
                        <tr>
                            <th>
                                <label>Send Enquiry Email to Customer As Well</label>
                            </th>
                            <td>
                                <FormToggle
                                    checked={data.gmpqcw_send_enquiry_email_cutomer === 'yes'}
                                    onChange={() =>
                                        updateField(
                                            'gmpqcw_send_enquiry_email_cutomer',
                                            data.gmpqcw_send_enquiry_email_cutomer === 'yes' ? 'no' : 'yes'
                                        )
                                    }
                                />
                            </td>
                        </tr>
                        <tr>
                            <th>
                                <label>Customer Email Subject</label>
                            </th>
                            <td>
                                <TextControl
                                    onChange={(value) => updateField('gmpqcw_customer_email_subject', value)}
                                    value={data.gmpqcw_customer_email_subject}
                                />
                            </td>
                        </tr>
                        <tr>
                            <th>
                                <label>Email Body</label>
                            </th>
                            <td>
                                <TextareaControl
                                    value={data.gmpqcw_email_body}
                                    onChange={(value) => updateField('gmpqcw_email_body', value)}
                                />
                                <p>
                                    {data.gmpqcw_field_customizer_enble &&
                                        Object.entries(data.gmpqcw_field_customizer_enble).map(([key]) => (
                                            <code key={key}>[{key}]</code>
                                        ))}
                                    <code>[product]</code>
                                   
                                </p>
                            </td>
                        </tr>
                    </table>
                    <Button isPrimary onClick={saveSettings} disabled={isSaving}>
                        {isSaving ? <Spinner /> : 'Save Settings'}
                    </Button>
                </>
            )}
        </PanelBody>
    );
};

export default EmailCustomizerTab;
