import { useEffect, useState } from 'react';
import {
    PanelBody,
    Button,
    Modal,
    TextControl,
    SelectControl,
    TextareaControl,
    CheckboxControl,
    RadioControl ,
    FormToggle ,
    __experimentalNumberControl as NumberControl,
    Spinner,
    ColorPalette,
    __experimentalText as Text
} from '@wordpress/components';
import { __ } from '@wordpress/i18n';
import { fetchData, saveData } from './api';
const GeneralSettingTab = ({ openPopup}) => {
    const [isSaving, setIsSaving] = useState(false);
    const [loading, setLoading] = useState(true);
    const [data, setData] = useState([]);
    const [modifiedFields, setModifiedFields] = useState({});
    const [redirectPages, setRedirectPages] = useState([]);
    useEffect(() => {
        // Fetch settings from the REST API
        fetchData(gmpqcw_wp_ajax.getsettings, gmpqcw_wp_ajax.nonce)
            .then((fetchedData) => {
                setData(fetchedData);
                setLoading(false);
            })
            .catch((error) => {
                setLoading(false);
                console.error('Error fetching settings:', error);
            });
    }, []);

    const updateField = (field, value) => {
        setData((prevData) => ({
            ...prevData,
            [field]: value,
        }));
        setModifiedFields((prevFields) => ({
            ...prevFields,
            [field]: value,
        }));
    };
    const toggleCheckbox = (fieldName, key) => {
        setData((prevData) => {
            const currentValues = prevData[fieldName] || [];
            const updatedValues = currentValues.includes(key)
                ? currentValues.filter((item) => item !== key) // Remove key if already present
                : [...currentValues, key]; // Add key if not present
            return {
                ...prevData,
                [fieldName]: updatedValues,
            };
        });
        setModifiedFields((prevFields) => ({
            ...prevFields,
            [fieldName]: data[fieldName]?.includes(key)
                ? data[fieldName].filter((item) => item !== key)
                : [...(data[fieldName] || []), key],
        }));
    };

    const saveSettings = () => {
        if (Object.keys(modifiedFields).length === 0) {
            openPopup('No changes to save.');
            return;
        }
        setIsSaving(true);

        saveData(gmpqcw_wp_ajax.savedata, gmpqcw_wp_ajax.nonce, modifiedFields)
            .then((responseData) => {
                setIsSaving(false);
                if (responseData.success) {
                    openPopup('Settings saved!');
                } else {
                    openPopup('Failed to save settings.');
                }
            })
            .catch((error) => {
                setIsSaving(false);
                console.error('Error saving settings:', error);
                openPopup('Failed to save settings.');
            });
    };
    return (
         <>
         {loading ? (
                <Spinner />
            ) : (
                <>
                    <PanelBody title="Setting">
                         <table className="GMadminTable">
                            <tr>
                                <th>
                                    <label>Users Show</label>
                                </th>
                                <td>
                                    <RadioControl
                                        selected={data.gmpqcw_usershow}
                                        options={ [
                                            { label: 'All Users', value: 'all' },
                                            { label: 'Only Logged in Users', value: 'logged_user' },
                                            { label: 'Only Logged out Users', value: 'logged_out' },
                                        ] }
                                        onChange={(value) => updateField('gmpqcw_usershow', value)}
                                       
                                    />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <label>Show Enquiry Button When Product is out of stock</label>
                                </th>
                                <td>
                                    <FormToggle
                                      checked={data.gmpqcw_show_product_outofstock === 'yes'}
                                      onChange={() =>
                                            updateField('gmpqcw_show_product_outofstock', data.gmpqcw_show_product_outofstock === 'yes' ? 'no' : 'yes')
                                        }
                                    />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <label>Remove Price From Product</label>
                                </th>
                                <td>
                                    <FormToggle
                                      checked={data.gmpqcw_remove_price === 'yes'}
                                      onChange={() =>
                                            updateField('gmpqcw_remove_price', data.gmpqcw_remove_price === 'yes' ? 'no' : 'yes')
                                        }
                                    />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <label>Hide Add to Cart Button</label>
                                </th>
                                <td>
                                    <FormToggle
                                      checked={data.gmpqcw_hide_add_to_cart === 'yes'}
                                      onChange={() =>
                                            updateField('gmpqcw_hide_add_to_cart', data.gmpqcw_hide_add_to_cart === 'yes' ? 'no' : 'yes')
                                        }
                                    />
                                </td>
                            </tr>
                         </table>
                    </PanelBody>
                    <PanelBody title="Form Setting">
                         <table className="GMadminTable">
                            <tr>
                                <th>
                                    <label>Button Background Color</label>
                                </th>
                                <td>
                                     <ColorPalette
                                         onChange={(value) => updateField('gmpqcw_enquiry_btn_bg_color', value)}
                                                    value={data.gmpqcw_enquiry_btn_bg_color}
                                      />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <label>Button Text Color</label>
                                </th>
                                <td>
                                     <ColorPalette
                                         onChange={(value) => updateField('gmpqcw_enquiry_btn_text_color', value)}
                                                    value={data.gmpqcw_enquiry_btn_text_color}
                                      />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <label>Button Background Hover Color</label>
                                </th>
                                <td>
                                     <ColorPalette
                                         onChange={(value) => updateField('gmpqcw_enquiry_btn_bg_hover_color', value)}
                                                    value={data.gmpqcw_enquiry_btn_bg_hover_color}
                                      />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <label>Button Text Hover Color</label>
                                </th>
                                <td>
                                     <ColorPalette
                                         onChange={(value) => updateField('gmpqcw_enquiry_btn_text_hover_color', value)}
                                                    value={data.gmpqcw_enquiry_btn_text_hover_color}
                                      />
                                </td>
                            </tr>
                             <tr>
                                <th>
                                    <label>Content Before Enquiry From</label>
                                </th>
                                <td>
                                    <TextareaControl
                                         onChange={(value) => updateField('gmpqcw_content_beforeform', value)}
                                                    value={data.gmpqcw_content_beforeform}
                                      />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <label>Content After Enquiry From</label>
                                </th>
                                <td>
                                    <TextareaControl
                                         onChange={(value) => updateField('gmpqcw_content_afterform', value)}
                                                    value={data.gmpqcw_content_afterform}
                                      />
                                </td>
                            </tr>
                            <tr>
                                <th>
                                    <label>Dom Event After Form Event</label>
                                </th>
                                <td>
                                    <pre>
    <code>
{`<script>
window.addEventListener('Gm_quote_cart_submitted', (event) => {
    const { success, formData, result } = event.detail;
    if (success) {
        console.log("Enquiry submitted successfully:", result);
    } else {
        console.error("Enquiry submission failed:", result);
    }
});
</script>`}
    </code>
</pre>
You can also trigger custom tracking pixels, marketing tags, or webhooks within this event handler after the form submission.
                                </td>
                            </tr>
                         </table>
                    </PanelBody>
                    <PanelBody title="Redirection Setting">
                         <table className="GMadminTable">
                            <tr>
                                <th>
                                    <label>Redirect after Enquiry form Submission</label>
                                </th>
                                <td>
                                    <FormToggle
                                      checked={data.gmpqcw_redirect_form_sub === 'yes'}
                                      onChange={() =>
                                            updateField('gmpqcw_redirect_form_sub', data.gmpqcw_redirect_form_sub === 'yes' ? 'no' : 'yes')
                                        }
                                    />
                                </td>
                            </tr>
                            {data.gmpqcw_redirect_form_sub === 'yes' && (
                            <tr>
                                <th>
                                    <label>Redirect Page</label>
                                </th>
                                <td>
                                    <SelectControl
                                        value={data.gmpqcw_redirect_form_sub_page}
                                        onChange={(value) => updateField('gmpqcw_redirect_form_sub_page', value)}
                                        options={[
                                            { disabled: true, label: 'Select an Option', value: '' },
                                            ...gmpqcw_wp_ajax.gmpqcwpages.map((page) => ({
                                                label: page.post_title, // Assuming `page.title` is the name of the page
                                                value: page.ID,    // Assuming `page.id` is the unique identifier for the page
                                            })),
                                        ]}
                                    />
                                    </td>
                            </tr>
                            )}
                            <tr>
                                <th>
                                    <label>Disable Woocommerce Cart and Checkout Page?</label>
                                </th>
                                <td>
                                    <FormToggle
                                      checked={data.gmpqcw_disable_cart_checkout_page === 'yes'}
                                      onChange={() =>
                                            updateField('gmpqcw_disable_cart_checkout_page', data.gmpqcw_disable_cart_checkout_page === 'yes' ? 'no' : 'yes')
                                        }
                                    />
                                </td>
                            </tr>
                            {data.gmpqcw_disable_cart_checkout_page === 'yes' && (
                            <tr>
                                <th>
                                    <label>Redirect Page</label>
                                </th>
                                <td>
                                    <SelectControl
                                        value={data.gmpqcw_redirect_page}
                                        onChange={(value) => updateField('gmpqcw_redirect_disable_cart_checkout_page', value)}
                                        options={[
                                            { disabled: true, label: 'Select an Option', value: '' },
                                            ...gmpqcw_wp_ajax.gmpqcwpages.map((page) => ({
                                                label: page.post_title, // Assuming `page.title` is the name of the page
                                                value: page.ID,    // Assuming `page.id` is the unique identifier for the page
                                            })),
                                        ]}
                                    />
                                   
                                    </td>
                            </tr>
                            )}

                         </table>
                         <Button isPrimary onClick={saveSettings} disabled={isSaving}>
                            {isSaving ? <Spinner /> : 'Save Settings'}
                        </Button>
                    </PanelBody>
                </>
            )}
        </>
    );  
};

export default GeneralSettingTab;
