import { useEffect, useState } from 'react';
import {
    PanelBody,
    Button,
    RadioControl,
    Spinner,
    CheckboxControl,
    __experimentalScrollable as Scrollable,
    FormToggle
} from '@wordpress/components';
import { fetchData, saveData } from './api';

const IncludeExcludeTab = ({ openPopup, setActiveTab }) => {
    const [isSaving, setIsSaving] = useState(false);
    const [loading, setLoading] = useState(true);
    const [data, setData] = useState({});
    const [modifiedFields, setModifiedFields] = useState({});

    useEffect(() => {
        fetchData(gmpqcw_wp_ajax.getsettings, gmpqcw_wp_ajax.nonce)
            .then((fetchedData) => {
                setData(fetchedData);
                setLoading(false);
            })
            .catch((error) => {
                setLoading(false);
                console.error('Error fetching settings:', error);
            });
    }, []);

    const updateField = (field, value) => {
        setData((prevData) => ({
            ...prevData,
            [field]: value,
        }));
        setModifiedFields((prevFields) => ({
            ...prevFields,
            [field]: value,
        }));
    };

    const toggleCheckbox = (fieldName, key) => {
        setData((prevData) => {
            const currentValues = prevData[fieldName] || [];
            const updatedValues = currentValues.includes(key)
                ? currentValues.filter((item) => item !== key)
                : [...currentValues, key];

            return {
                ...prevData,
                [fieldName]: updatedValues,
            };
        });

        setModifiedFields((prevFields) => ({
            ...prevFields,
            [fieldName]: data[fieldName]?.includes(key)
                ? data[fieldName].filter((item) => item !== key)
                : [...(data[fieldName] || []), key],
        }));
    };

    const saveSettings = () => {
        if (Object.keys(modifiedFields).length === 0) {
            openPopup('No changes to save.');
            return;
        }

        setIsSaving(true);
        saveData(gmpqcw_wp_ajax.savedata, gmpqcw_wp_ajax.nonce, modifiedFields)
            .then((responseData) => {
                setIsSaving(false);
                if (responseData.success) {
                    openPopup('Settings saved!');
                    setModifiedFields({});
                } else {
                    openPopup('Failed to save settings.');
                }
            })
            .catch((error) => {
                setIsSaving(false);
                console.error('Error saving settings:', error);
                openPopup('Failed to save settings.');
            });
    };

    return (
        <PanelBody>
            {loading ? (
                <Spinner />
            ) : (
                <>
                    <table className="GMadminTable">
                        <tbody>
                            <tr>
                                <th>
                                    <label>Include/Exclude Based On Category</label>
                                </th>
                                <td>
                                    <RadioControl
                                        selected={data.gmpqcw_include_exclude}
                                        options={[
                                            { label: 'All', value: 'all' },
                                            { label: 'Include', value: 'include' },
                                            { label: 'Exclude', value: 'exclude' },
                                        ]}
                                        onChange={(value) => updateField('gmpqcw_include_exclude', value)}
                                    />
                                </td>
                            </tr>
                            {data.gmpqcw_include_exclude === 'include' && (
                                <tr>
                                    <th>
                                        <label>Include From Category</label>
                                    </th>
                                    <td>
                                        <Scrollable
                                            scrollDirection="y"
                                            style={{ height: 300 }}
                                        >
                                            {gmpqcw_wp_ajax.gmpqcwcategory.map((catdata) => (
                                                <CheckboxControl
                                                    key={String(catdata.term_id)}
                                                    label={catdata.name}
                                                    checked={data.gmpqcw_include_category?.includes(String(catdata.term_id))}
                                                    onChange={() => toggleCheckbox('gmpqcw_include_category', String(catdata.term_id))}
                                                />
                                            ))}
                                        </Scrollable>
                                    </td>
                                </tr>
                            )}
                            {data.gmpqcw_include_exclude === 'exclude' && (
                                <tr>
                                    <th>
                                        <label>Exclude From Category</label>
                                    </th>
                                    <td>
                                        <Scrollable
                                            scrollDirection="y"
                                            style={{ height: 300 }}
                                        >
                                            {gmpqcw_wp_ajax.gmpqcwcategory.map((catdata) => (
                                                <CheckboxControl
                                                    key={String(catdata.term_id)}
                                                    label={catdata.name}
                                                    checked={data.gmpqcw_exclude_category?.includes(String(catdata.term_id))}
                                                    onChange={() => toggleCheckbox('gmpqcw_exclude_category', String(catdata.term_id))}
                                                />
                                            ))}
                                        </Scrollable>
                                    </td>
                                </tr>
                            )}
                        </tbody>
                    </table>
                    <Button isPrimary onClick={saveSettings} disabled={isSaving}>
                        {isSaving ? <Spinner /> : 'Save Settings'}
                    </Button>
                </>
            )}
        </PanelBody>
    );
};

export default IncludeExcludeTab;
